import React from 'react';
import type { ISplitButtonProps } from './interface';
import { BaseSplitButton} from './BaseSplitButton';
import { useToolbarItemConfig } from '../common/hooks/useToolbarItemConfig'

export type { ISplitButtonProps as ITBSplitButtonProps };
export const TBSplitButton = React.memo(React.forwardRef<HTMLDivElement, ISplitButtonProps>(
  (props, ref) => {
  
    const { itemSize } = useToolbarItemConfig();

    return itemSize === 'large' ? null : (
      <BaseSplitButton
        size={itemSize}
        ref={ref}
        {...props}
      />
    );
  },
));

TBSplitButton.displayName = 'splitButton';
